import styles from './ProductDetail.module.scss';
import classNames from 'classnames/bind';

import { useEffect, useRef, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import getProducts from 'apiServices/getProduct';
import Button from 'components/Button/Button';

const cx = classNames.bind(styles);

function ProductDetail() {
  // Xu ly khach hang chon so luong
  const [quantityInput, setQuantityInput] = useState(0);

  const [readMore, setReadMore] = useState(false);

  const products = useSelector((state) => state.rootReducer.products);
  const { ProductId } = useParams();
  const dispatch = useDispatch();

  useEffect(() => {
    window.scrollTo(0, 0);
    getProducts(dispatch);
  }, []);

  const product = products.find((item) => {
    return item.id === ProductId;
  });

  return (
    <div className={cx('wrapper')}>
      {product && (
        <div className={cx('product-detail')}>
          <div className={cx('product-image')}>
            <img src={product.image} alt="" />
          </div>
          <div className={cx('product-desc')}>
            <div className={cx('product-name')}>{product.name}</div>
            <div className={cx('product-price')}>{product.price}</div>
            <div className={cx('product-color')}>
              <p>Mu sc</p>
              <div className={cx('color-list')}>
                <input type="radio" name="color" id="white" value="white" className={cx('custom-radio', 'white')} />
                <input type="radio" name="color" id="black" value="black" className={cx('custom-radio', 'black')} />
                <input type="radio" name="color" id="blue" value="blue" className={cx('custom-radio', 'blue')} />
              </div>
            </div>
            <div className={cx('product-size')}>
              <p>Kch thc</p>
              <div className={cx('size-list')}>
                <div className={cx('size-item')}>
                  <input type="radio" name="size" id="M" value="M" />
                  <label htmlFor="M">M</label>
                </div>
                <div className={cx('size-item')}>
                  <input type="radio" name="size" id="L" value="L" />
                  <label htmlFor="L">L</label>
                </div>
                <div className={cx('size-item')}>
                  <input type="radio" name="size" id="XL" value="XL" />
                  <label htmlFor="XL">XL</label>
                </div>
              </div>
            </div>
            <div className={cx('product-quantity')}>
              <p>S lng</p>
              <div className={cx('select-quantity')}>
                <div
                  className={cx('select-quantity__btn')}
                  onClick={() => {
                    if (quantityInput > 0) {
                      setQuantityInput((prev) => prev - 1);
                    }
                  }}
                >
                  <box-icon name="minus"></box-icon>
                </div>
                <div className={cx('select-quantity__input')}>{quantityInput}</div>
                <div
                  className={cx('select-quantity__btn')}
                  onClick={() => {
                    if (quantityInput < 99) {
                      setQuantityInput((prev) => prev + 1);
                    }
                  }}
                >
                  <box-icon name="plus"></box-icon>
                </div>
              </div>
            </div>
            <div className={cx('product-choice')}>
              <button className={cx('product-btn')}>THM VO GI HNG</button>
              <button className={cx('product-btn')}>MUA NGAY</button>
            </div>
          </div>
          <div className={cx('product-info')}>
            <div className={readMore ? cx('info', 'more') : cx('info')}>
              <h3>CHI TIT SN PHM</h3>
              <h4 className={cx('info-name')}>Tn sn phm: {product.name}</h4>
              <p>---</p>
              <h4> Thng tin sn phm</h4>
              <div className={cx('info-group')}>
                <p>Mu sc: trng be, vng ct, en, xanh mint, ghi nht</p>
                <p>Kch c: 0/1/2/3 tng ng vi S/M/L/XL</p>
                <p>Cht liu: vi bamboo (thong mt v khng nhn)</p>
                <p>Xut x: Vit Nam</p>
              </div>
              <h4> QUI NH I HNG KHI MUA</h4>
              <div className={cx('info-group')}>
                <p>- Mi ho n ch c i mt ln. Bn c th i hng trong 14 ngy k t ngy mua hng</p>
                <p>
                  - Mt hng phi  trong tnh trng ban u, cn nguyn tem mc, cha qua s dng, cha git gi v c
                  ho n tng ng
                </p>
                <p>
                  - Bn vui lng gi li ho n  c i hng. Bn c th xut trnh ha n mua hng di dng giy
                  in hoc nh dng in t trn in thoi di ng ca bn
                </p>
                <p>
                  - SSSTUTTER khng p dng tr hng - hon tin di mi hnh thc (c trong trng hp ho n  mua
                  c gi tr cao hn ho n i)
                </p>
                <p>
                  - Vi cc sn phm gim di 30%, SSSTUTTER s h tr i hng v s lng sn phm i nhiu hn sn
                  phm tr, min tng ho n i bng hoc ln hn. Trong trng hp ho n i cao hn, bn vui lng
                  b thm phn chnh lch.
                </p>
                <p>
                  - Vi cc sn phm gim di 30%, SSSTUTTER s h tr i hng v s lng sn phm i nhiu hn sn
                  phm tr, min tng ho n i bng hoc ln hn. Trong trng hp ho n i cao hn, bn vui lng
                  b thm phn chnh lch.
                </p>
                <p>- Vi cc sn phm gim trn 30% v sn phm ph kin, SSSTUTTER s khng h tr i hng</p>
                <p>- Qu tng khng c i hoc qui ra tin, voucher.</p>
              </div>
              <h4> C QUYN KHI MUA  CA SSSTUTTER:</h4>
              <div className={cx('info-group')}>
                <p>- Sn phm ng vi hnh nh, thm ch p hn trn hnh, c nam v n mc ln u ph hp.</p>
                <p>
                  - Bao b nylon trng c c th thy c sn phm bn trong, ti ch chng ma v hn ch cc tc
                  ng vt l
                </p>
                <p>- Thip cm n, Card shop, ho n mua hng tiu chun chuyn nghip</p>
              </div>
              <h4> NHNG IU LU  KHI BO QUN O:</h4>
              <div className={cx('info-group')}>
                <p>- Khng  o  cc ni m v nn git o ngay sau khi s dng  trnh m mc;</p>
                <p>- Khng git chung o trng vi qun o mu;</p>
                <p>- Khng  trc tip bt git ln qun o khi git  trnh o b phai v loang mu;</p>
              </div>
            </div>
            <button
              className={cx('btn-info')}
              onClick={() => {
                console.log(readMore);
                setReadMore(!readMore);
              }}
            >
              {readMore ? 'Thu gn' : 'Xem thm'}
            </button>
          </div>
        </div>
      )}
    </div>
  );
}

export default ProductDetail;
